import Vue from 'vue'
import Router from 'vue-router'

//首页
import Index from '../components/IndexViews/Index'


//特效欣赏-普罗米修斯
import VFX from '../components/VFX/VFX'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/index"
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/VFX',
      name: 'VFX',
      component: VFX
    },
  ]
})



