<template>
  <section class="my-banner">
    <div class="container">
      <div class="banner" id="banner">
        <div class="banner-view"></div>
        <div class="banner-btn"></div>
        <div class="banner-number"></div>
        <div class="banner-progres"></div>
      </div>
    </div>
  </section>
</template>

<script>

  import {FragmentBanner} from '../../assets/js/banner'

  export default {
    name: "MyBanner",
    data() {
      return {
        imgs: [
          'https://mr-jemp.github.io/blogs/resourse/img/b1.jpg',
          'https://mr-jemp.github.io/blogs/resourse/img/b6.png',
          'https://mr-jemp.github.io/blogs/resourse/img/b3.jpg',
          'https://mr-jemp.github.io/blogs/resourse/img/b4.jpg',
          'https://mr-jemp.github.io/blogs/resourse/img/b5.jpg',
        ]
      }
    },
    mounted() {
      this.initBanner();
    },
    methods: {
      /**
       * 初始化banner
       */
      initBanner() {
        this.$nextTick(() => {
          var banner = new FragmentBanner({
            container: "#banner",//选择容器 必选
            imgs: this.imgs,//图片集合 必选
            size: {
              width: 1200,
              height: 500
            },//容器的大小 可选
            //行数与列数 可选
            grid: {
              line: 12,
              list: 14
            },
            index: 0,//图片集合的索引位置 可选
            type: 2,//切换类型 1 ， 2 可选
            boxTime: 5000,//小方块来回运动的时长 可选
            fnTime: 10000//banner切换的时长 可选
          });
        })
      }
    }
  }
</script>

<style>
  @import "../../assets/css/banner.css";
</style>



