<template>
  <section class="vfx">
    <!--侧边导航-->
    <aside class="aside-nav" @mouseenter="animateEnter" @mouseleave="animateLeave">
      <span class="caption">特效欣赏</span>
      <ul class="slide-nav">
        <li v-for="(item, index) in vfxList">
          <a @click="transmit(index)">{{item}}</a>
        </li>
      </ul>
    </aside>

    <!--特效页面引入-->
    <iframe src="https://mr-jemp.github.io/blogs/resourse/VFX/普罗米修斯.html" frameborder="no" border="0"
            scrolling="no">
    </iframe>
  </section>
</template>

<script>
  export default {
    name: "VFX",
    data() {
      return {
        vfxList: ["普罗米修斯", "3D烟花", "毕达哥加斯树", "超级三角", "变形粒子", "射线", "心"],
      }
    },
    methods: {
      animateEnter() {
        var $li = $('.slide-nav li');
        (function iteration(index) {
          if (index > $li.length) {
            return;
          }

          $li.eq(index).animate({
            height: 30,
          }, 100, function () {
            $(this).css("transform", "rotateZ(360deg)");
            iteration(index + 1);
          })
        })(0);
      },
      animateLeave() {
        var $li = $('.slide-nav li');
        (function iteration(index) {
          if (index < 0) {
            return;
          }

          $li.eq(index).animate({
            height: 0,
          }, 100, function () {
            $(this).css("transform", "rotateZ(-360deg)");
            iteration(index - 1);
          })
        })($li.length - 1);
      },
      /**
       * 跳转对应的特效页面
       * @param value HTML页面名
       */
      transmit(index) {
        var pageSrc = "https://mr-jemp.github.io/blogs/resourse/VFX/" + this.vfxList[index] + '.html';
        $("iframe").attr("src", pageSrc)
      }
    }
  }
</script>

<style scoped lang="less">
  .vfx {
    width: 100%;
    height: 100vh;

    .aside-nav {
      position: fixed;
      right: 50px;
      top: 200px;
      z-index: 999;
      width: 100px;

      .caption {
        display: block;
        height: 30px;
        color: #b7cefc;
        height: 30px;
        cursor: default;
        line-height: 30px;
        font-size: 15px;
        text-align: center;
        font-weight: 800;
        background-color: #000;
        -webkit-box-shadow: 0 0 10px #e5e5e5;
        -moz-box-shadow: 0 0 10px #e5e5e5;
        box-shadow: 0 0 10px #e5e5e5;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        border-radius: 10px;
      }

      ul.slide-nav {
        & > li {
          overflow: hidden;
          height: 0px;
          background-color: #000;
          -webkit-box-shadow: 0 0 10px #e5e5e5;
          -moz-box-shadow: 0 0 10px #e5e5e5;
          box-shadow: 0 0 10px #e5e5e5;
          -webkit-border-radius: 10px;
          -moz-border-radius: 10px;
          border-radius: 10px;
          -webkit-transition: .3s;
          -moz-transition: .3s;
          -ms-transition: .3s;
          -o-transition: .3s;
          transition: .3s;
          & > a {
            display: block;
            line-height: 30px;
            text-align: center;
            color: #f5f5f5;
            font-size: 13px;
            font-weight: 600;
            cursor: pointer;
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
          }
        }
      }
    }

    iframe {
      width: 100%;
      height: 100%;
    }

  }
</style>



